import { useState } from "react"
import fillForm from "../assets/images/herocontactpage.png"
import { Helmet } from "react-helmet"
import TransitionEffect from "../components/TransitionEffect"
import { Layout } from '../components/Layout'

const Contact = () => {
  const [formData, setFormData] = useState({
    name: "",
    email: "",
    message: ""
  })

  function handleChange(e) {
    const { name, value } = e.target;

    setFormData({
      ...formData,
      [name]: value,
    })
  }

  function clearForm() {
    setFormData({
      name: "",
      email: "",
      message: ""
    })
  }
  function handleSubmit(e) {
    e.preventDefault();

    console.log("form submitted", formData)

    clearForm();
  }
  return (
    <>
      <Helmet>
        <title>Contact</title>
        <meta name="description" content="Generated by create next app" />
      </Helmet>
      <TransitionEffect />
      <main className='w-full flex flex-col items-center justify-center dark:text-light'>
        <Layout className="pt-0 md:pt-16 sm:pt-8">
          <div className="flex items-center justify-between w-full lg:flex-col">

            <div className="w-1/2 md:w-full md:hidden">
              <img src={fillForm} alt="fill contact form" width={600} />
            </div>

            <div className="w-1/2 flex flex-col px-10 py-5  self-center lg:w-full lg:text-center">
              <h1 className="text-7xl font-bold">Contact me</h1>
              <form
                onSubmit={handleSubmit}
                className="mt-4 text-xl font-semibold capitalize"
              >
                <fieldset>
                  <div>
                    <label
                      htmlFor="name"
                    >name</label>
                  </div>
                  <div>
                    <input
                      type="text"
                      name="name"
                      value={formData.name}
                      onChange={handleChange}

                      className="border font-thin px-2 py-1 rounded-lg focus:outline-none"
                    />
                  </div>
                  <div>
                    <label
                      htmlFor="name"
                    >email</label>
                  </div>
                  <div>
                    <input
                      type="email"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}

                      className="border font-thin px-2 py-1 rounded-lg focus:outline-none"
                    />
                  </div>
                  <div>
                    <label
                      htmlFor="message"
                    >message</label>
                  </div>
                  <div>
                    <textarea
                      name="message"
                      value={formData.message}
                      onChange={handleChange}

                      rows={7}
                      cols={30}

                      className="border font-thin px-2 py-1 rounded-lg focus:outline-none"
                    />
                  </div>
                  <div>
                    <button
                      type="submit"
                      className="bg-primary text-light mt-2 py-2 px-6 rounded-lg text-lg font-semibold hover:bg-light hover:text-primary border-2 border-solid hover:border-primary"
                    >
                      Submit
                    </button>
                  </div>
                </fieldset>
              </form>
            </div>
          </div>
        </Layout>
      </main>
    </>
  )
}

export default Contact